/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-unused-vars */
import { VFStage } from './display/VFStage';
import { calculateUpdatePlayerSize, getBoundingClientRect } from './utils/CalculatePlayerSize';
import importScript from './utils/ImportScript';
import IEvent from './event/IEvent';
import { IVFOptions, EngineAPI } from './IVFEngine';
import { IVFDataV1, ScaleMode, VFStateCode, ITransitionData, SceneEvent } from './core/model/IVFData';
import Config from './core/Config';
import { EventType } from './event/EventType';
import { EventLevel } from './event/EventLevel';
import ErrorDisplay from './error/ErrorDisplay';

declare let VFBUILDDATE: any; // webpackprod

export class Player implements EngineAPI {
    /**
     * @private
     * @type {vf.Application}
     */
    public app: vf.Application;

    /**
     * @private
     * @type {VFStage}
     */
    private stage?: VFStage;

    /**
     * @private
     * @type {IVFDataV1}
     */
    private _data?: IVFDataV1;

    /**
     * 
     */
    private defaultScene?: { callBack: Function;params: any[] };
    /**
     * @private
     * @type {VFStage}
     */
    private _readyState: VFStateCode;
    public get readyState(): VFStateCode {
        return this._readyState;
    }
    public set readyState(value) {
        this._readyState = value;
        this.config.systemEvent.emit(EventType.STATUS, { code: value, level: EventLevel.STATUS, data: value });
    }

    /**
     * @private
     * @type {boolean}
     */
    private _errpanel: ErrorDisplay;

    /**
     * @private
     * @type {Config}
     */
    private config: Config;

    private option: IVFOptions;

    constructor(options: IVFOptions) {
        //  1. 
        this.option = options;
        this.config = new Config(options);

        // 2. 
        this.app = this.createApp();
        this._errpanel = new ErrorDisplay(this.config, options.useCustomErrorPanel);
        this.initSystemEvent();
        this._readyState = VFStateCode.INIT;

        //  3
        if (options.src) { this.play(options.src); }
    }

    private createApp(): vf.Application {
        const options = this.option;
        const config = this.config;

        // eslint-disable-next-line no-undef
        const app = new vf.Application({
            backgroundColor: parseInt(config.bgcolor || '0', 16),
            transparent: config.wmode === 'transparent',
            antialias: true,
            resolution: options.resolution,
            forceCanvas: options.forceCanvas,
            powerPreference: 'low-power'
        });

        const frameRate = options.frameRate || 30;
        app.ticker.maxFPS = frameRate;
        vf.Ticker.system.maxFPS = frameRate;
        vf.Ticker.shared.stop();
        vf.Ticker.shared.maxFPS = frameRate;
        vf.gui.TickerShared.maxFPS = frameRate;
        vf.gui.Utils.debug = options.debug || false;

        return app;
    }

    public async play(src?: any): Promise<void> {
        if (this._readyState !== VFStateCode.INIT
            && this._readyState !== VFStateCode.LOADED) {
            this.reload();
        }
        this.loadData(src);
    }

    public playData(src?: any, data?: any) {
        if (data) {
            this.config.conversionData = data;
        }

        this.play(src);
    }

    public pause(): void {
        if (this.stage) {
            this.stage.pause();
        }
    }

    public resume(): void {
        if (this.stage) {
            this.stage.resume();
        }
    }

    public reset(): void {
        if (this.stage) {
            this.stage.reset();
        }
    }

    public message(msg: IEvent): void {
        this.config.systemEvent.emit(EventType.ONMESSAGE, msg);
    }

    public runtimeLog(msg: IEvent): void {
        if (this.onMessage) {
            this.onMessage(msg);
        }
    }

    public switchToNextScene(transition?: ITransitionData): void {
        if (this.stage) {
            this.stage.switchToNextScene(transition);
        }
    }

    public switchToPrevScene(transition?: ITransitionData): void {
        if (this.stage) {
            this.stage.switchToPrevScene(transition);
        }
    }

    public switchToSceneId(sceneId: string, transition?: ITransitionData): void {
        sceneId = sceneId.toString();
        if (this.stage) {
            this.stage.switchToSceneId(sceneId, transition);
        }
        else {
            this.defaultScene = { callBack: this.switchToSceneId, params: [sceneId, transition] };
        }
    }

    public switchToSceneIndex(index: number, transition?: ITransitionData): void {
        if (this.stage) {
            this.stage.switchToSceneIndex(index, transition);
        }
        else {
            this.defaultScene = { callBack: this.switchToSceneIndex, params: [index, transition] };
        }
    }

    public dispose(removeView = false): void {
        if (this.readyState === VFStateCode.DISABLED) {
            return;
        }

        this.option = null as any;
        this.defaultScene = undefined;

        this.config.systemEvent.removeAllListeners();

        if (this.stage) {
            this.stage.dispose();
        }
        vf.AudioEngine.Ins().dispose();

        if (this.app && this.app.stage) {
            this.app.destroy(removeView, { children: true, texture: true, baseTexture: true });
        }
        this.stage = undefined;
        this.app = null as any;
        this.onDispose();
        this.readyState = VFStateCode.DISABLED;
    }

    /**
     * 
     */
    // eslint-disable-next-line handle-callback-err
    public readonly onError = (err: any) => {
        //
    };

    /**
     * 
     */
    public readonly onInit = () => {
        //
    };

    /**
     * 
     */
    public readonly onReady = () => {
        //
    };

    /**
     * 
     */
    public readonly onSceneLoad = () => {
        //
    };
    /**
     * 
     */
    public readonly onSceneCreate = () => {
        //
    };

    /**
     * 
     */
    public readonly onMessage = (msg: IEvent) => {
        //
    };

    /**
     * 
     */
    public readonly onDispose = () => {
        //
    };

    private initSystemEvent(): void {
        const event = this.config.systemEvent;

        event.on(EventType.MESSAGE, this.sendMessage, this);
        event.on(EventType.STATUS, this.onStatus, this);
    }

    private reload(): void {
        const config = this.config;

        config.systemEvent.removeAllListeners();

        vf.AudioEngine.Ins().dispose();

        if (this.stage) {
            this.stage.dispose();
        }

        if (this.app) {
            this.app.destroy(true, { children: true, texture: true, baseTexture: true });
        }
        this.stage = undefined;

        this.app = this.createApp();

        this.initSystemEvent();
    }

    /**
     * 
     * TODO
     *   1pixi
     *   2
     *   3VF..
     */
    private async start(): Promise<void> {
        if (!this._data) { return; }
        const config = this.config;
        const data = this._data;
        const container = this.config.container;

        const clientRect = getBoundingClientRect(container);

        // 0
        config.width = vf.gui.Utils.formatRelative(data.width, clientRect.width);
        config.height = vf.gui.Utils.formatRelative(data.height, clientRect.height);
        if (config.scaleMode === undefined) {
            config.scaleMode = this._data.scaleMode || ScaleMode.NO_SCALE;
        }
        config.output('Config Info', config.info);
        // 2PIXI
        const view = this.app.view;

        view.width = config.width;
        view.height = config.height;
        view.style.zIndex = '0';

        container.appendChild(this.app.view);

        // 3PXVF
        this.stage = new VFStage(this._data, this.config, this);
        // 4 
        // eslint-disable-next-line max-len
        calculateUpdatePlayerSize(container, this.app.view, this.stage, this.config.scaleMode as any, this.app.renderer.resolution);

        // 5API'vf[hashid] api is ready'
        this.readyState = VFStateCode.READY;

        // 6 
        if (this.defaultScene) {
            this.defaultScene.callBack.call(this, this.defaultScene.params[0], this.defaultScene.params[1]);
        }
        else {
            this.stage.start();
        }
    }

    private async loadData(src: any): Promise<void> {
        this.readyState = VFStateCode.LOAD;

        const onStatus = this.onStatus.bind(this);

        await this.config.i18n.load(this.config.cdns.default, onStatus);

        if (typeof src === 'string') {
            this._data = await vf.utils.readFileSync(src, { responseType: 'json' }).catch((value) => { onStatus(value); });
        }
        else if (typeof src === 'object') {
            this._data = src;
        }

        const data = this._data;

        if (data) {
            if (data.conversion) {
                if (typeof src === 'string') {
                    const version = src.split('?')[1] || 'v=0';
                    // tslint:disable-next-line: max-line-length
                    const conversionPath = `${src.substr(0, src.lastIndexOf('/'))}/${data.conversion}?${version}`;

                    await importScript(conversionPath).catch((e: IEvent) => {
                        this.onStatus(e);
                    });
                    const vFConversion = await new (window as any).VFConversion();

                    await vFConversion.analysis(this.config.conversionData, this._data);
                }
                else {
                    this.onStatus({ code: 'E0005', level: EventLevel.ERROR, data: data.conversion });
                }
            }
            this.readyState = VFStateCode.LOADED;
            await this.start();
        }
    }

    // -------------------------  ---------------------------//
    /**
     * 
     * @param msg
     */
    private sendMessage(msg: IEvent): void {
        this.onMessage(msg);
    }

    /**
     * 
     * @param msg
     */
    private onStatus(msg: IEvent): void {
        if (msg.level === EventLevel.ERROR || msg.level === EventLevel.WARNING) {
            if (msg.level === EventLevel.ERROR) {
                this._errpanel.setMessage(msg.code, msg.data);
            }
            msg.message = this._errpanel.getText(msg.code, msg.data);
            if (this.onError) {
                this.onError(msg);
            }

            return;
        }
        switch (msg.code) {
            case VFStateCode.INIT:
                if (this.onInit) {
                    this.onInit();
                }
                break;
            case VFStateCode.READY:
                if (this.onReady) {
                    this.onReady();
                }
                break;
            case SceneEvent.SceneLoad:
                if (this.onSceneLoad) {
                    this.onSceneLoad();
                }
                break;
            case SceneEvent.ScenComplete:
                if (this.onSceneCreate) {
                    this.onSceneCreate();
                }
                break;
            default:
                if (this.onMessage) {
                    this.onMessage(msg);
                }
        }
    }
}
const w = (window as any);

if (w.vf === undefined) {
    w.vf = {};
}

w.vf.player = { Player };
